.angle-border {
  position: relative;
}

.angle-border::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 30%;
  height: 100%;
  border-left: 8px solid #b37142;
  border-top: 8px solid #b37142;
}

.angle-border::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 70%;
  height: 100%;
  z-index: 1;
  border-bottom: 8px solid #b37142;
}

