.border-left {
  position: relative;
}

.border-left::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0%;
  width: 8px;
  height: 100%;
  border-left: 6px solid #b37142;
}

.border-right {
  position: relative;
}

.border-right::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-right: 6px solid #b37142;
}